body {
  max-width: 1920px;
  width: 100%;
  background-color: #fff;
  justify-content: center;
  font-size: 16px;
}

.swiper__hero {
  position: relative;
}

.img-slide__mob {
  display: none;
}

.wrap__slide {
  width: 100%;
  z-index: 1;
  justify-content: space-between;
  display: flex;
  position: absolute;
}

.img__left-slide, .wrap__img {
  position: relative;
}

.wrap__rigth-slide, .wrap__left-slide {
  background-position: 0;
  display: block;
  position: relative;
}

.wrap__rigth-slide {
  background-position: 100%;
}

.img-slide__desc {
  position: relative;
}

.title__right {
  width: 100%;
  height: 100%;
  color: #fff;
  text-transform: uppercase;
  background-color: rgba(47, 48, 58, .4);
  padding-top: 300px;
  padding-left: 0%;
  font-family: Fiver;
  font-size: 130px;
  font-weight: 400;
  line-height: .5;
  position: absolute;
  top: 0%;
}

.title__left {
  width: 100%;
  height: 100%;
  color: #fff;
  text-transform: uppercase;
  text-align: right;
  background-color: rgba(47, 48, 58, .4);
  padding-top: 300px;
  font-family: Fiver;
  font-size: 130px;
  font-weight: 400;
  line-height: .5;
  position: absolute;
  top: 0;
}

.logo {
  z-index: 3;
  position: absolute;
  top: 20px;
  left: 20px;
}

@font-face {
  font-family: Transit Cat;
  src: local(Transit Cat Regular), local(TransitCat-Regular), url("Transit CAT.73e385c0.woff2") format("woff2"), url("Transit CAT.b9c2236b.woff") format("woff"), url("Transit CAT.57211e8d.eot") format("eot"), url("Transit-CAT.73c90167.ttf") format("ttf");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: deserta;
  src: local(deserta Regular), local(deserta-Regular), url("deserta.b27ba14c.woff2") format("woff2"), url("deserta.faa0ee4a.woff") format("woff"), url("deserta.71f248ff.eot") format("eot"), url("deserta.5b01b43a.ttf") format("ttf");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: JournalSans;
  src: local(JournalSans Regular), local(JournalSans-Regular), url("journalSans.2fc638d4.woff2") format("woff2"), url("JournalSans.04192f8a.woff") format("woff"), url("JournalSans-Italic.2a777719.woff") format("woff"), url("JournalSans-Italic.5228c5ca.ttf") format("ttf"), url("JournalSans.c7b3c982.eot") format("eot"), url("JournalSans.98db453b.ttf") format("ttf");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: Fiver;
  src: local(Fiver Regular), local(Fiver-Regular), url("Fiver.b279d9ff.woff2") format("woff2"), url("Fiver.6706445d.woff") format("woff"), url("Fiver.bdd3641f.eot") format("eot"), url("Fiver.f23e485f.ttf") format("ttf");
  font-weight: normal;
  font-style: normal;
}

@font-face {
  font-family: Arial-Black;
  src: local(Arial Black), local(Arial-Black), url("Arial-Black.abaf37ef.woff") format("woff"), url("Arial-Black.b62333b5.eot") format("eot"), url("Arial-Black.adb2dd6e.ttf") format("ttf");
  font-weight: normal;
  font-style: normal;
}

.hero__section {
  background-color: #000;
  position: absolute;
  overflow: hidden;
}

.hero {
  background-color: #000;
}

.hero > .hero__container {
  width: 100%;
  justify-content: space-between;
  padding: 20px;
  display: flex;
  position: absolute;
  overflow: hidden;
}

.hero__inner {
  width: 100%;
  justify-content: space-between;
  padding: 20px;
  display: flex;
  position: relative;
}

.logo__content {
  z-index: 3;
  position: absolute;
  top: 20px;
  left: 15px;
}

.menu-button__list {
  z-index: 1;
  width: 50%;
  text-transform: uppercase;
  flex-flow: column wrap;
  gap: 70px;
  display: flex;
  position: relative;
}

.menu-button__item {
  width: 131px;
  flex-direction: column;
  align-items: center;
  gap: 10px;
  display: flex;
}

.menu__button {
  color: #fff;
  text-align: center;
  letter-spacing: .06em;
  cursor: pointer;
  background: none;
  font-size: 30px;
  font-weight: 700;
  line-height: 1.2;
  display: inline-block;
}

.menu-button__item:nth-child(1) {
  padding-left: 90%;
}

.menu-button__item:nth-child(2) {
  padding-left: 75%;
}

.menu-button__item:nth-child(3), .menu-button__item:nth-child(4) {
  padding-left: 50%;
}

.menu-button__item:nth-child(5) {
  padding-left: 75%;
}

.menu-button__item:nth-child(6) {
  padding-left: 90%;
}

.list-item__text {
  color: #fff;
  font-size: 30px;
  line-height: 1.2;
}

.wrap__button {
  position: absolute;
  top: 50%;
  left: 48%;
}

.button__menu-open {
  width: 127px;
  height: 127px;
  cursor: pointer;
  z-index: 3;
  background-color: rgba(0, 0, 0, .7);
  border: 3px solid #fff;
  border-radius: 100%;
  justify-content: center;
  align-items: center;
  display: flex;
  position: absolute;
  top: 50%;
  left: 48%;
  transform: rotate(180deg);
}

.page__menu {
  width: 50%;
  height: 100%;
  display: flex;
  position: absolute;
  top: 10px;
  right: 20px;
}

.page__list {
  text-align: right;
  height: 100%;
  flex-direction: column;
  justify-content: space-between;
  display: flex;
}

.page__inner {
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
}

.page__contacts {
  width: 100%;
  transition: transform .8s;
  transform: translateY(-93%);
}

.page-contacts__container {
  width: 100%;
  height: 100%;
  display: flex;
}

.page-contacts__address {
  z-index: 1;
  flex-direction: column;
  gap: 50px;
  padding-top: 110px;
  padding-left: 200px;
  display: flex;
  position: relative;
}

.mail__link, .phone__link, .address__description {
  color: #fff;
  margin-bottom: 50px;
  font-family: inherit;
  font-size: 18px;
  line-height: 1.5;
}

.phone__link, .mail__link {
  margin-bottom: 0;
}

.phone__list {
  flex-direction: column;
  gap: 5px;
  display: flex;
}

.page__item-contacts {
  display: block;
  position: absolute;
  top: 20px;
  right: 20px;
}

.address__map {
  width: 620px;
}

.contacts__date {
  flex-direction: row;
  gap: 30%;
  display: flex;
}

.wrap__button-contacts {
  z-index: 2;
  padding: 0;
  position: absolute;
  bottom: 20px;
  right: 0;
}

.button__contacts {
  flex-direction: column;
  justify-content: center;
  align-items: flex-end;
  gap: 5px;
  display: flex;
  position: relative;
}

.contacts-toggle:focus:before {
  content: "";
  width: 900px;
  height: 2px;
  background-color: #fff;
  display: block;
}

.before {
  content: "";
  width: 850px;
  height: 2px;
  background-color: #fff;
  display: block;
}

.contacts__icon {
  cursor: pointer;
  bottom: 3px;
  right: 0;
}

.contacts-toggle {
  text-align: right;
  text-transform: uppercase;
  cursor: pointer;
  color: #fff;
  background-color: #000;
  border: none;
  outline: none;
  font-size: 24px;
  font-weight: 400;
  transition: transform 1s;
}

.page__about-us {
  width: 100%;
  height: 100%;
  transition: transform .8s;
  position: absolute;
  transform: translateY(92%);
}

.wrap__about-us-container {
  max-width: 850px;
  flex-direction: column;
  gap: 50px;
  padding-top: 80px;
  padding-left: 200px;
  display: flex;
}

.page-about-us__container {
  position: absolute;
  top: 60px;
  left: 80px;
  right: 20px;
}

.wrap__button-about-us {
  outline: none;
  position: absolute;
  top: 20px;
  right: 0;
}

.icon__about-us {
  cursor: pointer;
  transform: rotate(-90deg);
}

.button__about-us {
  flex-direction: column;
  justify-content: center;
  align-items: flex-end;
  gap: 5px;
  display: flex;
  position: relative;
}

.js-open-about-us:focus + .icon__about-us {
  display: block;
}

.js-open-about-us {
  text-align: right;
  text-transform: uppercase;
  cursor: pointer;
  color: #fff;
  z-index: 1;
  background-color: #000;
  border: none;
  outline: none;
  font-size: 24px;
  font-weight: 400;
  line-height: 1.2;
}

.js-open-about-us:visited, .js-open-about-us:focus:after {
  content: "";
  width: 800px;
  height: 2px;
  z-index: 1;
  background-color: #fff;
  margin-bottom: -5px;
  display: block;
}

.about-us__inner {
  flex-direction: column;
  gap: 30px;
  display: flex;
}

.about-us-item__text, .about-us__text {
  color: #fff;
  font-family: JournalSans;
  font-size: 16px;
  font-weight: 400;
  line-height: 1.7;
}

.about-us__list {
  padding-left: 20px;
}

.about-us-item__text {
  line-height: 1.6;
}

.ceo__block {
  flex-direction: row;
  gap: 20px;
  display: flex;
}

.ceo__text {
  color: #fff;
  font-family: JournalSans;
  font-weight: 400;
}

.ceo__text1 {
  font-size: 16px;
  line-height: 1.5;
}

.ceo__role {
  font-size: 20px;
  line-height: 1.5;
}

.ceo__name {
  font-size: 24px;
  line-height: 1.5;
}

.swiper__hero {
  position: absolute;
  top: 0;
}

.img-slide__mob {
  display: none;
}

.wrap__slide {
  z-index: 1;
  height: 900px;
  justify-content: space-between;
  transition: transform 1.2s;
  display: flex;
  position: relative;
  overflow: hidden;
}

.left-slide {
  position: relative;
}

.wrap__img {
  object-fit: cover;
  position: relative;
}

.wrap__rigth-slide {
  background-position: 100%;
  transition: transform 1s;
  animation: animationRight 1s linear infinite alternate forwards;
  display: block;
  position: relative;
}

.wrap__left-slide {
  background-position: 0;
  transition: transform 1.5s;
  animation: animationLeft 1.5s linear infinite alternate forwards;
  display: block;
  position: relative;
}

.title__right {
  max-width: 1920px;
  width: 100%;
  height: 100%;
  color: #fff;
  text-transform: uppercase;
  background-color: rgba(47, 48, 58, .4);
  padding-top: 300px;
  padding-left: 0%;
  font-family: Fiver;
  font-size: 130px;
  font-weight: 400;
  line-height: .5;
  position: absolute;
  top: 0%;
}

.title__left {
  width: 100%;
  height: 100%;
  color: #fff;
  text-transform: uppercase;
  text-align: right;
  background-color: rgba(47, 48, 58, .4);
  padding-top: 300px;
  font-family: Fiver;
  font-size: 130px;
  font-weight: 400;
  line-height: .5;
  position: absolute;
  top: 0%;
}

@media screen and (min-width: 480px) {
  .faq {
    max-width: 700px;
  }

  .faq__container {
    padding: 0 20px;
  }

  .flaq__content {
    padding: 20px;
  }

  .faq__title {
    text-transform: uppercase;
    color: #7c7a7a;
    justify-content: flex-end;
    font-family: Josefin Sans;
    font-size: 72px;
    font-weight: 700;
    display: flex;
  }

  .wrap-flaf__list {
    justify-content: center;
    align-items: center;
    padding-top: 10px;
    display: flex;
  }

  .flaq__list {
    flex-direction: column;
    gap: 30px;
    display: flex;
  }

  .flaq__link {
    color: red;
    text-align: left;
    font-family: Josefin Sans;
    font-size: 26px;
    font-weight: 700;
  }

  .flaq__link:after {
    content: "";
    height: 2px;
    background-color: #000;
    margin-top: 5px;
    display: block;
    left: 0;
  }
}

.description-house {
  width: 100%;
  height: 100%;
}

.description-house__contant {
  width: 100%;
  height: 100%;
  padding: 20px 30px;
}

.video__container {
  position: relative;
}

.poster-overlay {
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, .4);
  transition: opacity .3s;
  position: absolute;
  top: 0;
  left: 0;
}

.poster-overlay.active {
  opacity: 0;
}

.video {
  width: 100%;
  height: 100%;
  display: block;
  position: relative;
}

.play__button {
  z-index: 1;
  width: 60px;
  height: 60px;
  cursor: pointer;
  background-color: rgba(0, 0, 0, 0);
  border: none;
  border-radius: 50%;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.play-button__icon {
  content: "";
  border-width: 15px 0;
  border-color: #fff;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-40%, -50%);
}

.write-us__contant {
  max-width: 505px;
  padding: 30px;
  position: relative;
}

.forma__container {
  width: 100%;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 30px;
  padding: 20px 0;
  display: flex;
  position: relative;
}

.write-us__title {
  color: #515151;
  font-size: 48px;
  font-weight: 700;
}

.write-us__forma {
  flex-direction: column;
  justify-content: center;
  align-items: center;
  display: flex;
}

.write-us__wrap-input {
  width: 100%;
  flex-direction: column;
  font-size: 16px;
  display: flex;
  position: relative;
}

.write-us__input {
  color: #000;
  border: none;
  border-bottom: 1px solid #000;
  outline: none;
  font-family: JournalSans;
  font-size: 16px;
  font-weight: 400;
}

.select__region {
  color: #000;
  letter-spacing: 1.2px;
  border: none;
  border-bottom: 1px solid #000;
  font-size: 16px;
}

.forma__radio {
  justify-content: space-between;
  align-items: center;
  gap: 120px;
  padding: 10px 0;
  display: flex;
}

.input__radio {
  width: 20px;
  height: 20px;
  font-size: 16px;
}

.label__radio {
  color: #000;
  flex-direction: column;
  align-items: center;
  gap: 5px;
  font-size: 25px;
  display: flex;
}

.forma__button {
  text-transform: uppercase;
  letter-spacing: 2px;
  background-color: rgba(0, 0, 0, 0);
  margin-top: 10px;
  padding: 25px 110px;
  font-size: 24px;
  display: inline-block;
}

@media screen and (min-width: 480px) {
  .swiper.my__swiper {
    z-index: 33;
    width: 100%;
    height: 100%;
    flex-direction: row;
    gap: 5px;
    display: flex;
    position: relative;
  }

  .my__swiper:before {
    content: "";
    width: 100%;
    height: 100%;
    z-index: -1;
    background-color: #000;
    position: absolute;
    top: 0;
    left: 0;
  }

  .swiper__wrapper {
    width: 100%;
    height: 100%;
    transition-property: transform;
    transition-timing-function: var(--swiper-wrapper-transition-timing-function, initial);
    box-sizing: content-box;
    z-index: 31;
    justify-content: center;
    align-items: center;
    display: flex;
    position: relative;
  }

  .swiper__container {
    padding: 20px 0;
  }

  .swiper__slide {
    width: 100%;
    height: 100%;
  }

  .page__section {
    min-width: 480px;
    max-width: 682px;
    height: auto;
    background: #fff;
    border-radius: 4px;
    align-items: center;
    margin: 0 auto;
    display: inline-block;
    position: fixed;
    top: 0;
    left: 50%;
    transform: translateX(-50%);
  }

  .close-page__button {
    cursor: pointer;
    width: 50px;
    height: 50px;
    background-color: rgba(0, 0, 0, 0);
    border: 1px solid rgba(0, 0, 0, .1);
    border-radius: 50%;
    margin: 0 auto;
    position: absolute;
    top: 15px;
    right: 15px;
  }

  .close-page__icon {
    z-index: 2;
    cursor: pointer;
    fill: #000;
    background-position: center;
    background-repeat: no-repeat;
    background-size: contain;
    background-origin: border-box;
    justify-content: center;
    align-items: center;
    display: inline-block;
    position: absolute;
    transform: translate(-50%, -50%);
  }

  .page__container {
    padding: 0 15px 40px;
    position: relative;
  }

  .page-header__container {
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    gap: 1px;
    padding-top: 40px;
    display: flex;
    position: relative;
  }

  .logo__content-mob {
    flex-direction: column;
    align-items: center;
    display: flex;
  }

  .logo__text {
    color: #000;
    font-family: deserta, sans-serif;
    font-size: 28px;
    line-height: 1.2;
  }

  .mob-menu__button {
    width: 120px;
    height: 120px;
    cursor: pointer;
    z-index: 3;
    background-color: #000;
    border: 3px solid #fff;
    border-radius: 100%;
    justify-content: center;
    align-items: center;
    display: flex;
    position: absolute;
    left: 54%;
    transform: translateX(-50%);
  }

  .icon__phone {
    width: 74px;
    height: 74px;
    position: absolute;
    right: 0;
  }

  .page__links {
    height: 120px;
    justify-content: space-between;
    margin-top: 20px;
    margin-bottom: 20px;
    display: flex;
  }

  .page__link {
    color: #000;
    text-transform: uppercase;
    font-size: 36px;
    line-height: 1.1;
  }

  .page__item-aboutus {
    margin-left: 10px;
    transform: rotate(-90deg)translateY(-30%);
  }

  .page-item-contact {
    margin-right: -180px;
    transform: rotate(-90deg)translateY(-50%);
  }

  .swiper-pagination {
    bottom: 50px;
    left: 25%;
  }

  .wrap__pagination {
    align-items: center;
    display: flex;
    position: relative;
  }

  .pagination__list {
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    gap: 5px;
    padding: 10px 0;
    display: flex;
    position: relative;
  }

  .pagination__item {
    width: 77px;
    height: 77px;
    cursor: pointer;
    color: #000;
    background-color: rgba(0, 0, 0, 0);
    border: 1px solid #000;
    border-radius: 50%;
    justify-content: center;
    align-items: center;
    display: flex;
  }

  .pagination__number {
    color: #000;
    font-size: 16px;
    font-weight: bold;
  }
}

@media screen and (min-width: 480px) {
  .selling__contant {
    flex-direction: column;
    justify-content: center;
    padding-top: 20px;
    padding-left: 20px;
    display: flex;
  }

  .selling__img {
    flex-direction: row;
    align-items: flex-end;
    margin: 0;
    display: flex;
  }

  .figcaption__text {
    color: #5b5b5b;
    letter-spacing: .08em;
    font-size: 30px;
    font-weight: 700;
    display: flex;
    transform: rotate(-90deg)translateY(-70%)translateX(30%);
  }

  .selling__text {
    max-width: 450px;
    color: #000;
    padding: 20px 0;
    font-size: 18px;
    line-height: 1.4;
    display: flex;
  }
}

*, :before, :after {
  box-sizing: border-box;
}

html {
  -webkit-text-size-adjust: 100%;
  tab-size: 4;
  line-height: 1.15;
}

hr {
  height: 0;
  color: inherit;
}

abbr[title] {
  -webkit-text-decoration: underline dotted;
  text-decoration: underline dotted;
}

b, strong {
  font-weight: bolder;
}

code, kbd, samp, pre {
  font-size: 1em;
}

small {
  font-size: 80%;
}

sub, sup {
  vertical-align: baseline;
  font-size: 75%;
  line-height: 0;
  position: relative;
}

sub {
  bottom: -.25em;
}

sup {
  top: -.5em;
}

table {
  text-indent: 0;
  border-color: inherit;
}

button, input, optgroup, select, textarea {
  margin: 0;
  font-family: inherit;
  font-size: 100%;
  line-height: 1.15;
}

button, select {
  text-transform: none;
}

button, [type="button"], [type="reset"], [type="submit"] ::-moz-focus-inner {
  border-style: none;
  padding: 0;
}

:-moz-focusring {
  outline: 1px dotted ButtonText;
}

:-moz-ui-invalid {
  box-shadow: none;
}

legend {
  padding: 0;
}

progress {
  vertical-align: baseline;
}

::-webkit-inner-spin-button, ::-webkit-outer-spin-button {
  height: auto;
}

[type="search"] {
  outline-offset: -2px;
}

::-webkit-search-decoration {
  -webkit-appearance: none;
}

::-webkit-file-upload-button {
  -webkit-appearance: button;
  font: inherit;
}

summary {
  display: list-item;
}

*, :after, :before {
